export default function TimeForm() {
  const classes = useStyles();
  // const theme = useTheme();

  const [state, setState] = React.useContext(GlobalState);
  const [bookingPackage, setBookingPackage] = React.useState(
    state.bookingPackage ?? ""
  );

  const [packages, setPackages] = React.useState([
    { id: "15_minutes", text: "15-minutes GP", price: 75 },
    { id: "extended", text: "Extended GP", price: 125 },
    { id: "follow_up", text: "Follow-up GP", price: 50 },
  ]);

  const [dataLoaded, setDataLoaded] = React.useState(false);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const boxClicked = (key) => {
    if (key) {
      setBookingPackage(key);
      setState({ ...state, bookingPackage: key });
    }
  };

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom className={classes.title}>
        Pick a Package
      </Typography>
      <React.Fragment>
        <Grid container spacing={2} alignItems="center" justify="center">
          {packages.map((item) => (
            <Grid item xs={6} sm={4}>
              <div
                className={
                  state.bookingPackage === item.id
                    ? classes.catBoxSelected
                    : classes.catBox
                }
                onClick={() => boxClicked(item.id)}
              >
                <Grid
                  container
                  alignItems="center"
                  justify="center"
                  style={{ height: "100%", width: "100%" }}
                >
                  <Grid item>
                    {item.text}

                    <div style={{ fontSize: "1.5rem", paddingTop: "5px" }}>
                      £{item.price}
                    </div>
                  </Grid>
                </Grid>
              </div>
            </Grid>
          ))}
        </Grid>
      </React.Fragment>
    </React.Fragment>
  );
}
